___CSS_LOADER_EXPORT___.push([module.id, `.header {
    margin: auto;
    padding: 0.5rem 1rem;
    width: 100%;
    min-height: 4rem;

    display: grid;
    grid-template-columns: auto repeat(2, 1fr);
    gap: 1rem;
    cursor: default;
    user-select: none;

    background-color: #faf9f8;
    /* box-shadow: 0 0 4px 0 grey; */
}

.hamburger,
.group-name-and-date-holder,
.misc-options-holder {
    align-self: center;
}

.hamburger {
    cursor: pointer;
}

.group-name {
    font-size: 1.25rem;
}

.date-holder {
    font-size: 0.75rem;
}

.misc-options-holder {
    width: max-content;
    justify-self: end;

    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
    cursor: pointer;

    display: flex;
    gap: 0.25rem;

    transition: all linear 100ms;
}

.misc-options-holder:hover {
    background-color: #e0e0e0;
}
